/** @file WebsocketProvider.js
 * @authors:
 *   Fabian Vogelsteller <fabian@ethereum.org>
 * @date 2017
 */

const _ = require('lodash')
const errors = require('../../../caver-core-helpers').errors

let Ws = null
let _btoa = null
let parseURL = null
/* eslint-disable no-undef */
if (typeof window !== 'undefined' && typeof window.WebSocket !== 'undefined') {
    Ws = window.WebSocket
    _btoa = btoa
    parseURL = function(url) {
        return new URL(url)
    }
} else {
    Ws = require('websocket').w3cwebsocket
    _btoa = function(str) {
        return Buffer.from(str).toString('base64')
    }
    const url = require('url')
    if (url.URL) {
        // Use the new Node 6+ API for parsing URLs that supports username/password
        const newURL = url.URL
        parseURL = function(u) {
            return new newURL(u)
        }
    } else {
        parseURL = require('url').parse
    }
}
// Default connection ws://localhost:8546

const WebsocketProvider = function WebsocketProvider(url, options) {
    const _this = this
    this.responseCallbacks = {}
    this.notificationCallbacks = []

    options = options || {}
    this._customTimeout = options.timeout

    // The w3cwebsocket implementation does not support Basic Auth
    // username/password in the URL. So generate the basic auth header, and
    // pass through with any additional headers supplied in constructor
    const parsedURL = parseURL(url)
    const headers = options.headers || {}
    const protocol = options.protocol || undefined
    if (parsedURL.username && parsedURL.password) {
        headers.authorization = `Basic ${_btoa(`${parsedURL.username}:${parsedURL.password}`)}`
    }

    // Allow a custom client configuration
    const clientConfig = options.clientConfig || undefined

    this.connection = new Ws(url, protocol, undefined, headers, undefined, clientConfig)
    this.reconnect = () => new Ws(url, protocol, undefined, headers, undefined, clientConfig)

    this.addDefaultEvents()

    // LISTEN FOR CONNECTION RESPONSES
    this.connection.onmessage = function(e) {
        const data = typeof e.data === 'string' ? e.data : ''

        _this._parseResponse(data).forEach(function(result) {
            let id = null

            // get the id which matches the returned id
            if (_.isArray(result)) {
                result.forEach(function(load) {
                    if (_this.responseCallbacks[load.id]) {
                        id = load.id
                    }
                })
            } else {
                id = result.id
            }

            // notification
            if (!id && result.method.indexOf('_subscription') !== -1) {
                _this.notificationCallbacks.forEach(function(callback) {
                    if (_.isFunction(callback)) {
                        callback(result)
                    }
                })

                // fire the callback
            } else if (_this.responseCallbacks[id]) {
                _this.responseCallbacks[id](null, result)
                delete _this.responseCallbacks[id]
            }
        })
    }
}

/**
 Will add the error and end event to timeout existing calls

 @method addDefaultEvents
 */
WebsocketProvider.prototype.addDefaultEvents = function() {
    const _this = this

    this.connection.onerror = function() {
        _this._timeout()
    }

    this.connection.onclose = function() {
        _this._timeout()

        // reset all requests and callbacks
        _this.reset()
    }

    // this.connection.on('timeout', function(){
    //     _this._timeout();
    // });
}

/**
 Will parse the response and make an array out of it.

 @method _parseResponse
 @param {String} response
 */
WebsocketProvider.prototype._parseResponse = function(response) {
    const _this = this
    const returnValues = []

    // DE-CHUNKER
    const dechunkedData = response
        .replace(/\}[\n\r]?\{/g, '}|--|{') // }{
        .replace(/\}\][\n\r]?\[\{/g, '}]|--|[{') // }][{
        .replace(/\}[\n\r]?\[\{/g, '}|--|[{') // }[{
        .replace(/\}\][\n\r]?\{/g, '}]|--|{') // }]{
        .split('|--|')

    dechunkedData.forEach(function(data) {
        // prepend the last chunk
        if (_this.lastChunk) {
            data = _this.lastChunk + data
        }

        let result = null

        try {
            result = JSON.parse(data)
        } catch (e) {
            _this.lastChunk = data

            // start timeout to cancel all requests
            clearTimeout(_this.lastChunkTimeout)
            _this.lastChunkTimeout = setTimeout(function() {
                _this._timeout()
                throw errors.InvalidResponse(data)
            }, 1000 * 15)

            return
        }

        // cancel timeout and set chunk to null
        clearTimeout(_this.lastChunkTimeout)
        _this.lastChunk = null

        if (result) {
            returnValues.push(result)
        }
    })

    return returnValues
}

/**
 Adds a callback to the responseCallbacks object,
 which will be called if a response matching the response Id will arrive.

 @method _addResponseCallback
 */
WebsocketProvider.prototype._addResponseCallback = function(payload, callback) {
    const id = payload.id || payload[0].id
    const method = payload.method || payload[0].method

    this.responseCallbacks[id] = callback
    this.responseCallbacks[id].method = method

    const _this = this

    // schedule triggering the error response if a custom timeout is set
    if (this._customTimeout) {
        setTimeout(function() {
            if (_this.responseCallbacks[id]) {
                _this.responseCallbacks[id](errors.ConnectionTimeout(_this._customTimeout))
                delete _this.responseCallbacks[id]
            }
        }, this._customTimeout)
    }
}

/**
 Timeout all requests when the end/error event is fired

 @method _timeout
 */
WebsocketProvider.prototype._timeout = function() {
    for (const key in this.responseCallbacks) {
        if (Object.prototype.hasOwnProperty.call(this.responseCallbacks, key)) {
            this.responseCallbacks[key](errors.InvalidConnection('on WS'))
            delete this.responseCallbacks[key]
        }
    }
}

WebsocketProvider.prototype.send = function(payload, callback) {
    const _this = this

    if (this.connection.readyState === this.connection.CONNECTING) {
        setTimeout(function() {
            _this.send(payload, callback)
        }, 10)
        return
    }

    // try reconnect, when connection is gone
    // if(!this.connection.writable)
    //     this.connection.connect({url: this.url});
    if (this.connection.readyState !== this.connection.OPEN) {
        console.error('connection not open on send()')
        if (typeof this.connection.onerror === 'function') {
            this.connection.onerror(new Error('connection not open'))
        } else {
            console.error('no error callback')
        }
        // reconnect
        this.connection = this.reconnect()
        callback(new Error('connection not open. try reconnect..'))
        return
    }

    this.connection.send(JSON.stringify(payload))
    this._addResponseCallback(payload, callback)
}

/**
 Subscribes to provider events.provider

 @method on
 @param {String} type    'notifcation', 'connect', 'error', 'end' or 'data'
 @param {Function} callback   the callback to call
 */
WebsocketProvider.prototype.on = function(type, callback) {
    if (typeof callback !== 'function') {
        throw new Error('The second parameter callback must be a function.')
    }

    switch (type) {
        case 'data':
            this.notificationCallbacks.push(callback)
            break

        case 'connect':
            this.connection.onopen = callback
            break

        case 'end':
            this.connection.onclose = callback
            break

        case 'error':
            this.connection.onerror = callback
            break

        // default:
        //     this.connection.on(type, callback);
        //     break;
    }
}

// TODO add once

/**
 Removes event listener

 @method removeListener
 @param {String} type    'notifcation', 'connect', 'error', 'end' or 'data'
 @param {Function} callback   the callback to call
 */
WebsocketProvider.prototype.removeListener = function(type, callback) {
    const _this = this

    switch (type) {
        case 'data':
            this.notificationCallbacks.forEach(function(cb, index) {
                if (cb === callback) {
                    _this.notificationCallbacks.splice(index, 1)
                }
            })
            break

        // TODO remvoving connect missing

        // default:
        //     this.connection.removeListener(type, callback);
        //     break;
    }
}

/**
 Removes all event listeners

 @method removeAllListeners
 @param {String} type    'notifcation', 'connect', 'error', 'end' or 'data'
 */
WebsocketProvider.prototype.removeAllListeners = function(type) {
    switch (type) {
        case 'data':
            this.notificationCallbacks = []
            break

        // TODO remvoving connect properly missing

        case 'connect':
            this.connection.onopen = null
            break

        case 'end':
            this.connection.onclose = null
            break

        case 'error':
            this.connection.onerror = null
            break

        default:
            // this.connection.removeAllListeners(type);
            break
    }
}

/**
 Resets the providers, clears all callbacks

 @method reset
 */
WebsocketProvider.prototype.reset = function() {
    this._timeout()
    this.notificationCallbacks = []

    // this.connection.removeAllListeners('error');
    // this.connection.removeAllListeners('end');
    // this.connection.removeAllListeners('timeout');

    this.addDefaultEvents()
}

WebsocketProvider.prototype.supportsSubscriptions = function() {
    return true
}

module.exports = WebsocketProvider
